import { PipelineStage } from './stages'
import * as apiTypes from '../../types/data-item-types'

/**
 * @builder
 */
export interface SortStage extends PipelineStage {
  ascending(...fields: string[]): SortStage
  descending(...fields: string[]): SortStage
}

export class SortStageImpl implements SortStage {
  private sortFields: apiTypes.Sorting[] = []

  ascending(...fields: string[]): SortStage {
    fields.forEach((field) => {
      this.sortFields.push({ fieldName: field, order: apiTypes.SortOrder.ASC })
    })
    return this
  }

  descending(...fields: string[]): SortStage {
    fields.forEach((field) => {
      this.sortFields.push({ fieldName: field, order: apiTypes.SortOrder.DESC })
    })
    return this
  }

  toProto(): apiTypes.Stage {
    return { sort: { values: this.sortFields } }
  }
}
